/* eslint-disable jsx-a11y/no-onchange */
// eslint-disable-next-line jsx-a11y/no-onchange
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { adminRoot } from "../../../constants/defaultValues";
import jupyter from "../../../assets/jupyter.png";
import jupyterLab from "../../../assets/jupyterLab.png";
import vsCode from "../../../assets/vscode.png";
import RStudio from "../../../assets/RStudio.jpg";
import zeppelin from "../../../assets/zeppelin.png";
import spark from "../../../assets/spark.png";
import labelstudio from "../../../assets/labelstudio.svg";
import mixpanel from "mixpanel-browser";

const NotebookCreate = () => {
  const [isCustom, setisCustom] = useState(true);
  const [customVal, setCustomVal] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const [name, setName] = useState("");
  const [img, setImg] = useState(null);
  const [env, setEnv] = useState("");
  const [cpus, setCpus] = useState("");
  const [mem, setMem] = useState("");
  const [resources, setResources] = useState("");

  const envImages = [
    [
      {
        imageName: "Katonic Studio",
        imageVal: "katonic/katonic-studio:3.0"
      },
      {
        imageName: "Katonic-base",
        imageVal: "katonic/notebook-env:base"
      },
      {
        imageName: "PyTorch",
        imageVal: "katonic/lab-env:torchcpu1.8.1"
      },
      {
        imageName: "Tensorflow-2",
        imageVal: "katonic/lab-env:tf2.4"
      },
      {
        imageName: "Tensorflow-1",
        imageVal: "katonic/lab-env:tf1.4"
      },
      {
        imageName: "Scikit-learn",
        imageVal: "katonic/lab-env:scikt-learn"
      },
    ],
    [
      {
        imageName: "PyTorch",
        imageVal: "katonic/notebook-env:torchcpu1.8.1"
      },
      {
        imageName: "Tensorflow-2",
        imageVal: "katonic/notebook-env:tf2.4"
      },
      {
        imageName: "Tensorflow-1",
        imageVal: "katonic/notebook-env:tf1.4"
      },
      {
        imageName: "Scikit-learn",
        imageVal: "katonic/notebook-env:scikt-learn"
      },
    ],
    [
      {
        imageName: "VSCode",
        imageVal: "katonic/vscode:latest"
      },
      {
        imageName: "VSCode(Python)",
        imageVal: "katonic/vscode:miniconda"
      }
    ],
    [
      {
        imageName: "RStudio-base",
        imageVal: "katonic/rstudio:base"
      },
      {
        imageName: "R-tidyverse",
        imageVal: "katonic/rstudio-tidyverse"
      }
    ],
    null,
    null
  ]

  useEffect(() => {
    if (img !== "katonic") {
      setisCustom(false);
    } else {
      setisCustom(true);
    }
  }, [img]);
  const bodyUrl = `/platform/api/notebook/new`;
  const sendData = async (e) => {
    // mixpanel.track("Notebook Created");
    const Data = {
      stsName: name,
      image: img === "katonic" ? customVal : img,
      workspaceEnv: env,
      resources: resources
    };
    setIsLoading(true);
    e.preventDefault();
    await fetch(bodyUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(Data),
    });
    setIsLoading(false);
    history.push(`${adminRoot}/workspace`);
  };

  const handleNameInput = (e) => {
    let key = e.key;
    if (!/[a-z0-9\-]/.test(key)) {
      e.preventDefault();
    }
  }

  return (
    <div className="container-fluid">
      <div className="row justify-content-center">
        <div className="col-lg-7 col-md-12 mb-4">
          <div className="card mb-4">
            <div className="card-body">
              <h2 className="card-title">Create Workspace</h2>
              <form id="nb-create-form">
                <label htmlFor className="form-group has-float-label">
                  <input
                    onCopy={(e) => { e.preventDefault(); return false; }}
                    onPaste={(e) => { e.preventDefault(); return false; }}
                    onKeyDown={handleNameInput}
                    onChange={(e) => setName(e.target.value)}
                    value={name}
                    pattern="[a-z0-9-]+"
                    id="notebookName"
                    type="text"
                    className="form-control name-input"
                    required
                    placeholder="Use: a-z, 0-9 and -(hyphen)"
                  />
                  <span>Name</span>
                </label>

                <p style={{ fontSize: "13px" }}>Select Environment</p>
                <div className="environment">
                  <label>
                    <input
                      type="radio"
                      value="0"
                      name="env"
                      checked={env === "0"}
                      onChange={(e) => setEnv(e.target.value)}
                      className="freeLab"
                    />
                    <img src={jupyterLab} alt="Jupyter Lab" />
                  </label>
                  <label>
                    <input
                      type="radio"
                      required
                      value="1"
                      name="env"
                      checked={env === "1"}
                      onChange={(e) => setEnv(e.target.value)}
                      className="freeLab"
                    />
                    <img className="imgLab" src={jupyter} alt="Jupyter" />
                  </label>
                  <label>
                    <input
                      type="radio"
                      required
                      value="2"
                      name="env"
                      checked={env === "2"}
                      onChange={(e) => setEnv(e.target.value)}
                      className="freeLab"
                    />
                    <img className="imgLab" src={vsCode} alt="VS Code" />
                  </label>
                  <label>
                    <input
                      type="radio"
                      required
                      value="3"
                      name="env"
                      checked={env === "3"}
                      onChange={(e) => setEnv(e.target.value)}
                      className="upgradeLab"
                      disabled
                    />
                    <img className="imgLab" src={RStudio} alt="R Studio" />
                    <p className="hide">Upgrade to access this feature!</p>
                  </label>
                  <label>
                    <input
                      type="radio"
                      required
                      value="4"
                      name="env"
                      checked={env === "4"}
                      onChange={(e) => {
                        setEnv(e.target.value);
                        setImg("katonic/notebook-env:pyspark");
                      }}
                      className="upgradeLab"
                      disabled
                    />
                    <img className="imgLab" src={spark} alt="Spark" />
                    <p className="hide">Upgrade to access this feature!</p>
                  </label>
                  {/* <label>
                    <input
                      type="radio"
                      required
                      value="5"
                      name="env"
                      checked={env === "5"}
                      onChange={(e) => {
                        setEnv(e.target.value);
                        setImg("katonic/labelstudio:latest");
                      }}
                      className="freeLab"
                    />
                    <img className="imgLab" src={labelstudio} alt="Label Studio" />
                  </label> */}
                </div>
                {env === "0" ? (
                  <label htmlFor className="form-group has-float-label">
                    <select
                      name={img}
                      onChange={(e) => setImg(e.target.value)}
                      id="nb-env"
                      className="form-control select2-single name-input"
                      data-width="100%"
                      required="true"
                    >
                      <option value="">Select Image</option>
                      {envImages[Number(env)].map(envImage => (
                        <option value={envImage.imageVal}>
                          {envImage.imageName}
                        </option>
                      ))}
                      <option onClick={() => setisCustom(true)} value="katonic">
                        Custom Image
                      </option>
                    </select>
                    <span>Image</span>
                  </label>
                ) : null}
                {env === "1" ? (
                  <label htmlFor className="form-group has-float-label">
                    <select
                      name={img}
                      onChange={(e) => setImg(e.target.value)}
                      id="nb-env"
                      className="form-control select2-single name-input"
                      data-width="100%"
                      required="true"
                    >
                      <option value="">Select Image</option>
                      {envImages[Number(env)].map(envImage => (
                        <option value={envImage.imageVal}>
                          {envImage.imageName}
                        </option>
                      ))}
                    </select>
                    <span>Image</span>
                  </label>
                ) : null}
                {env === "2" ? (
                  <label htmlFor className="form-group has-float-label">
                    <select
                      name={img}
                      onChange={(e) => setImg(e.target.value)}
                      id="nb-env"
                      className="form-control select2-single name-input"
                      data-width="100%"
                      required="true"
                    >
                      <option value="">Select Image</option>
                      {envImages[Number(env)].map(envImage => (
                        <option value={envImage.imageVal}>
                          {envImage.imageName}
                        </option>
                      ))}
                    </select>
                    <span>Image</span>
                  </label>
                ) : null}
                {env === "3" ? (
                  <label htmlFor className="form-group has-float-label">
                    <select
                      name={img}
                      onChange={(e) => setImg(e.target.value)}
                      id="nb-env"
                      className="form-control select2-single name-input"
                      data-width="100%"
                      required="true"
                    >
                      <option value="">Select Image</option>
                      {envImages[Number(env)].map(envImage => (
                        <option value={envImage.imageVal}>
                          {envImage.imageName}
                        </option>
                      ))}
                    </select>
                    <span>Image</span>
                  </label>
                ) : null}


                {isCustom && (
                  <label htmlFor className="form-group has-float-label">
                    <input
                      onChange={(e) => setCustomVal(e.target.value)}
                      id="customImage"
                      name="image"
                      type="text"
                      value={customVal}
                      className="form-control name-input"
                    />
                    <span>Custom Image</span>
                  </label>
                )}
                <div className="row">
                  <div className="col-lg-12 col-md-12">
                    <label htmlFor className="form-group has-float-label">
                      <select
                        name="resources"
                        onChange={(e) => setResources(e.target.value)}
                        id="nb-env"
                        className="form-control select2-single name-input"
                        data-width="100%"
                        required="true"
                      >
                        <option value="">Select Resources</option>
                        <option value="small">Small (2Gi memory, 1 core cpu)</option>
                        <option disabled value="medium">Medium (4Gi memory, 2 core cpu)</option>
                        <option disabled value="large">Large (8Gi memory, 4 core cpu)</option>
                        <option disabled value="xLarge">XLarge (16Gi memory, 4 core cpu)</option>
                        <option disabled value="xxLarge">XXLarge (32Gi memory, 8 core cpu)</option>
                      </select>
                      <span>Resources</span>
                    </label>
                  </div>
                  {/* <div className="col-lg-6 col-md-12">
                    <label htmlFor className="form-group has-float-label">
                      <select
                        onChange={(e) => setMem(e.target.value)}
                        id="nb-memory"
                        name={mem}
                        className="form-control select2-single name-input"
                        data-width="100%"
                        required
                      >
                        <option value="">Select Memory</option>
                        <option value="1.0">1.0Gi</option>
                        <option value="2.0">2.0Gi</option>
                        <option value="4.0">4.0Gi</option>
                        <option value="6.0">6.0Gi</option>
                        <option value="8.0">8.0Gi</option>
                        <option value="10.0">10.0Gi</option>
                        <option value="12.0">12.0Gi</option>
                        <option value="14.0">14.0Gi</option>
                        <option value="8.0">8.0Gi</option>
                        <option value="16.0">16.0Gi</option>
                      </select>
                      <span>Memory</span>
                    </label>
                  </div> */}
                </div>
                <input
                  className="btn btn-primary font-weight-bold"
                  type="submit"
                  value="Create"
                  onClick={sendData}
                  disabled={
                    name === "" ||
                      env === "" ||
                      resources === ""
                      ? true
                      : false
                  }
                />
                {isLoading === true ? <div className="loading" /> : ""}
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default NotebookCreate;
